:root {
  background-color: rgb(206, 103, 103);
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: 'Bruno Ace', cursive;
}

.container {
  color: rgb(96, 95, 95);
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: 20% 40% 10% 30%;
  align-items: center;
  justify-content: center;
  width: 70vw;
  height: 50vh;
  background-color: rgb(241, 197, 74);
  border-radius: 40px;
  box-shadow: 15px 15px 50px black;
}

.containerBotao {
  display: grid;
  justify-content: center;
  grid-column: span 2;
  align-items: center;
}

.botao {
  border: none;
  background-color: rgb(94, 92, 92);
  color: white;
  border-radius: 10px;
  font-size: 20px;
  width: 30vw;
  }

  .resultado {
    grid-column: span 2;
    text-align: center;
    font-size: 30px;
}